var graceRichText = require("../../static/richText.js");
import uniNumberBox from '@/components/uni-number-box.vue';
import share from '@/components/share';
export default{
	components: {
		share,
		uniNumberBox
	},
	data() {
		return {
			specClass: 'none',
			specSelected:[],
			favorite: false,
			shareList: [],
			goods_price:0,//商品价格
			line_price:0,//
			stock_num:1,//库存
			commodityDetails:{},//商品详情信息
			imgList: [], //商品详情顶部轮播图列表
			contentHtml:'',//后台返回的HTML代码
			specList: [],  //尺寸 和颜色
			specChildList: [],
			ContrastList: [],
			goodsStatus:10,//是否支持购买
			goods_id:'',
			number:1,//商品数量
			specSkuId:'',
			//购买的时候需要传递的参数
			goodsId:'',//商品id
			goodsSkuId:'',//规格：比如：57_40
			allStockNum:0,
			tIndex:0,
			goodsSkuIda:'',
			goodsSkuIdb:'',
			goodsSkuIdc:'',
			goodsSkuIdd:'',
			goodsSkuIde:'',
			goodsSkuIdf:''
		};
	},
	async onLoad(options){
		let self = this;
		//接收传值 
		let id = options.id;
		this.$data.goods_id=id+'';
		// this.$api.msg(`点击了${id}`);
		this.goods_id = id;
		//获取商品详情
		
		let newSpecChildList = [];
		self.$axios(self.$baseUrl.getCommodityDetails,{
			goods_id:id,
		})
		.then(res =>{
			let resulrt = res.data.data.detail;
			if(res.data.code == 1){
				self.imgList = resulrt.imgs_url;
				self.commodityDetails = resulrt;
				self.allStockNum = resulrt.all_stock_num;
				self.goods_price = resulrt.spec[0].goods_price;
				self.line_price =resulrt.spec[0].line_price;
				self.stock_num =resulrt.spec[0].stock_num;
				self.goodsStatus = resulrt.goods_status;
				self.specSkuId = resulrt.spec[0].spec_sku_id;
				if(res.data.data.specData){
					self.ContrastList = res.data.data.specData.spec_list;
				}
				
				//购买时需要传递的参数
				self.goodsId = resulrt.spec[0].goods_id ;
				// self.goodsSkuId = resulrt.spec[0].spec_sku_id;
				
				 // 强化 richText    
				self.contentHtml = graceRichText.format(resulrt.content);
				let specAttr = res.data.data.specData.spec_attr;
				if(specAttr != '' || specAttr !=null || specAttr != 'undefined'){
					
					//拿到类型 规则名称
					specAttr.forEach((item,index)=>{
						self.specList.push({
							id: index+1,
							name: item.group_name,
							
						});
					})
					//应
					specAttr.forEach((item,index)=>{
						item.spec_items.forEach((todo,k)=>{
							newSpecChildList.push({
								id: k,
								pid: index+1,
								name: todo.spec_value,
								item_id:todo.item_id
							})
						})
					})
					
					//类型详情
					newSpecChildList.forEach((item,index)=>{
						self.specChildList.push({
							id: index +1,
							pid: item.pid,
							name:item.name,
							itemId:item.item_id
						});
					})
					
					
					//规格 默认选中第一条
					self.specList.forEach(item=>{
						for(let cItem of self.specChildList){
							if(cItem.pid === item.id){
								self.$set(cItem, 'selected', true);
								
								self.specSelected.push(cItem);
								break; //forEach不能使用break
							}
						}
					})
					
				}
				
			}
			
		},(error) =>{
			console.log(error)
		})
		
		//判断当前商品是否收藏过了
		this.isCollection(id);
	},
	  /**
	   * 生命周期函数--监听页面卸载
	   */
	  onUnload: function () {
		if(!this.favorite){
			let self=this;
			uni.setStorage({
				key:'needRemoveGoodsId',
				data:self.goods_id
			})
		}
	  },
	methods:{
		
		//规格弹窗开关
		toggleSpec(index) {
			if(this.specClass === 'show'){
				this.specClass = 'hide';
				setTimeout(() => {
					this.specClass = 'none';
				}, 250);
			}else if(this.specClass === 'none'){
				this.specClass = 'show';
			}
			if(index == 1){
				this.tIndex = 1;
			} else if(index == 2){
				this.tIndex = 2;
			}
		},
		//确认按钮
		toggleSpecTab(){
			if(this.specClass === 'show'){
				this.specClass = 'hide';
				setTimeout(() => {
					this.specClass = 'none';
				}, 250);
			}else if(this.specClass === 'none'){
				this.specClass = 'show';
			}
			
			if(this.tIndex == 1){//立即购买
				this.orderNow();
			}else if(this.tIndex == 2){//加入购物车
				
				this.addCart();
			}
		},
		//选择规格
		selectSpec(a,b,index, pid){
			
			let todo = 0;
			if(!Number(this.specSkuId)){
				 todo = this.specSkuId.split('_').length;
				this.goodsSkuIda = this.specSkuId.split('_')[0];
				this.goodsSkuIdb = this.specSkuId.split('_')[1];
				this.goodsSkuIdc = this.specSkuId.split('_')[2];
				this.goodsSkuIdd = this.specSkuId.split('_')[3];
				this.goodsSkuIde = this.specSkuId.split('_')[4];
				this.goodsSkuIdf = this.specSkuId.split('_')[5];
			}else{
				 todo = 1;
			}
			
			if(a == 0){
				this.goodsSkuIda = b.itemId;
			}else if(a == 1){
				this.goodsSkuIdb = b.itemId;
			}else if(a == 2){
				this.goodsSkuIdc = b.itemId;
			}else if(a == 3){
				this.goodsSkuIdd = b.itemId;
			}else if(a == 4){
				this.goodsSkuIde = b.itemId;
			}else if(a == 5){
				this.goodsSkuIdf = b.itemId;
			}
			if(todo == 1){
				this.specSkuId = this.goodsSkuIda;
			}else if(todo == 2){
				this.specSkuId = this.goodsSkuIda+'_'+this.goodsSkuIdb;
			}else if(todo == 3){
				this.specSkuId = this.goodsSkuIda+'_'+this.goodsSkuIdb+'_'+this.goodsSkuIdc;
			}else if(pid == 4){
				this.specSkuId = this.goodsSkuIda+'_'+this.goodsSkuIdb+'_'+this.goodsSkuIdc+'_'+this.goodsSkuIdd;
			}else if(todo == 5){
				this.specSkuId = this.goodsSkuIda+'_'+this.goodsSkuIdb+'_'+this.goodsSkuIdc+'_'+this.goodsSkuIdd+'_'+this.goodsSkuIde;
			}else if(todo == 6){
				this.specSkuId = this.goodsSkuIda+'_'+this.goodsSkuIdb+'_'+this.goodsSkuIdc+'_'+this.goodsSkuIdd+'_'+this.goodsSkuIde+'_'+this.goodsSkuIdf;
			}
			let list = this.specChildList;
			list.forEach(item=>{
				if(item.pid === pid){
					this.$set(item, 'selected', false);
				}
			})
			this.ContrastList.forEach((item)=>{
				if(item.spec_sku_id == this.specSkuId){
					this.stock_num = item.form.stock_num;
				}
			})
			this.$set(list[index], 'selected', true);
			//存储已选择
			/**
			 * 修复选择规格存储错误
			 * 将这几行代码替换即可
			 * 选择的规格存放在specSelected中
			 */
			this.specSelected = []; 
			list.forEach(item=>{ 
				if(item.selected === true){ 
					this.specSelected.push(item); 
				} 
			})
			
		},
		//分享
		share(){
			uni.share({  
			    provider: "weixin",  
			    scene: "WXSceneSession",  
			    type: 0,  
			    href: "http://baidu.com/",  
			    title: "37喵 为追剧而生",  
			    summary: "我正在使用追剧助手，赶紧跟我一起来体验！",  
			    imageUrl: "http://baidu.com/Content/img/jiazai.png",  
			    success: function (res) {  
			        console.log("success:" + JSON.stringify(res));  
			    },  
			    fail: function (err) {  
			        console.log("fail:" + JSON.stringify(err));  
			    }  
			});  
		},
		//收藏
		toFavorite(){
			
			if(this.favorite){
				this.cancelCollection(this.$data.goods_id);
			}else{
				this.addCollection(this.$data.goods_id);
			}
		},
		stopPrevent(){},
		isCollection(id){
			//判断当前的商品是否收藏过了
			let self=this;
			self.$axios(self.$baseUrl.isCollection,{
				goods_id:id+''
			})
			.then(res =>{
				
				if(res.data.code == 1){
					self.$data.favorite=(res.data.data.status==1);
				}else{
					self.$api.msg(res.data.msg);
				}
			},(error) =>{
				console.log(error)
			})
		},
		addCollection(id){
			uni.showLoading({
				title:'正在提交...'
			})
			//判断当前的商品是否收藏过了
			let self=this;
			self.$axios(self.$baseUrl.addCollection,{
				goods_id:id
			})
			.then(res =>{
				uni.hideLoading();
				if(res.data.code == 1){
					self.$data.favorite=true;
				}else{
					self.$api.msg(res.data.msg);
				}
			},(error) =>{
				uni.hideLoading();
			})
		},
		cancelCollection(id){
			//判断当前的商品是否收藏过了
			uni.showLoading({
				title:'正在提交...'
			})
			let self=this;
			self.$axios(self.$baseUrl.cancelCollection,{
				goods_id:id
			})
			.then(res =>{
				uni.hideLoading();
				if(res.data.code == 1){
					self.$data.favorite=false;
				}else{
					self.$api.msg(res.data.msg);
				}
			},(error) =>{
				uni.hideLoading();
				console.log(error)
			})
		},
		//数量
		numberChange(data){
			this.number = data.number;
		},
		
		//加入购物车
		addCart(){
			let self=this;
			self.$axios(self.$baseUrl.addCart,{
				goods_id:self.goods_id,
				goods_num:self.number,
				goods_sku_id:self.specSkuId
			})
			.then(res =>{
				if(res.data.code == 1){
					uni.showToast({title:'加入购物车成功',icon:'success'});
				}
			},(error) =>{
				console.log(error)
			})
		},
		//购买商品
		orderNow(){
			let goodsId = this.goodsId;
			let goodsSkuId = this.specSkuId;
			let goodsNum = this.number;
			uni.navigateTo({
				url: `/pages/cart/neworderdetails?goodsId=${goodsId}&goodsSkuId=${goodsSkuId}&goodsNum=${goodsNum}`
			})
		}
	},

}
